
import {onMounted, watch} from "vue";

export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    showTime: {
      type: Number,
      default: 3000
    }
  },
  setup(props: any, context: any) {
    onMounted(() => {
      setTimeout(() => {
        context.emit("update:visible", false);
      }, props.showTime);

      watch(props, () => {
        if (!props.visible) return;
        setTimeout(() => {
          context.emit("update:visible", false);
        }, props.showTime);
      });
    });
  }
};
